<template>
    <div class="main">
        
        
        <div class="container pz10">
            <div class="main_title pz10">
                <h4>笔记</h4>
            </div>
            <div class="main1 pz10">
                <div class="main1_title"><span>Git</span></div>
                <div class="main1_box">
                    <ul class="flexbox">
                        <li class="flex1" v-for="item in git_list">
                            <h4>
                                <!-- <a :href="item.url">{{item.name}}</a> -->
                                <router-link :to="{path:'/detail/'+item.id}">{{item.title}}</router-link>
                            </h4>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="main1 pz10">
                <div class="main1_title"><span>Vue</span></div>
                <div class="main1_box">
                    <ul class="flexbox">
                        <li class="flex1" v-for="item in vue_list">
                            <h4>
                                <!-- <a :href="item.url">{{item.name}}</a> -->
                                <router-link :to="{path:'/detail/'+item.id}">{{item.title}}</router-link>
                            </h4>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="main1 pz10">
                <div class="main1_title"><span>JS</span></div>
                <div class="main1_box">
                    <ul class="flexbox">
                        <li class="flex1" v-for="item in js_list">
                            <h4>
                                <!-- <a :href="item.url">{{item.name}}</a> -->
                                <router-link :to="{path:'/detail/'+item.id}">{{item.title}}</router-link>
                            </h4>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="main1 pz10">
                <div class="main1_title"><span>Canvas</span></div>
                <div class="main1_box">
                    <ul class="flexbox">
                        <li class="flex1" v-for="item in canvas_list">
                            <h4>
                                <!-- <a :href="item.url">{{item.name}}</a> -->
                                <router-link :to="{path:'/detail/'+item.id}">{{item.title}}</router-link>
                            </h4>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="main1 pz10">
                <div class="main1_title"><span>小程序</span></div>
                <div class="main1_box">
                    <ul class="flexbox">
                        <li class="flex1" v-for="item in mini_list">
                            <h4>
                                <!-- <a :href="item.url">{{item.name}}</a> -->
                                <router-link :to="{path:'/detail/'+item.id}">{{item.title}}</router-link>
                            </h4>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="main1 pz10">
                <div class="main1_title"><span>混合</span></div>
                <div class="main1_box">
                    <ul class="flexbox">
                        <li class="flex1" v-for="item in blend_list">
                            <h4>
                                <!-- <a :href="item.url">{{item.name}}</a> -->
                                <router-link :to="{path:'/detail/'+item.id}">{{item.title}}</router-link>
                            </h4>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
// import myHeader from '../common/header.vue'
// import Swiper from 'swiper';
import $ from 'jquery';
export default {
    name:'index',
    data(){
        return{
            // Git
            git_list:[],
            // Vue
            vue_list:[],
            // JS
            js_list:[],
            // Cavans
            canvas_list:[],
            // 小程序
            mini_list:[],
            // 混合
            blend_list:[]

            
        }
    },
    created(){
        var _this = this;
        this.$http.get(this.host+'/Indexgit/index').then(res=>{
            console.log(res);
            var data_all = res.data.data_list;
            var gitlist = _this.git_list;
            var vuelist = _this.vue_list;
            var jslist = _this.js_list;
            var canvaslist = _this.canvas_list;
            var minilist = _this.mini_list;
            var blendlist = _this.blend_list;
            for(var i=0;i<data_all.length;i++){
                if(data_all[i].label =='Git'){
                    gitlist.push(data_all[i]);
                   
                }else if(data_all[i].label =='Vue'){
                    vuelist.push(data_all[i]);
                   
                }else if(data_all[i].label =='JS'){
                    jslist.push(data_all[i]);
                    
                }else if(data_all[i].label =='小程序'){
                    minilist.push(data_all[i]);
                    
                }else if(data_all[i].label =='Canvas'){
                    canvaslist.push(data_all[i]);
                    
                }else if(data_all[i].label =='混合'){
                    blendlist.push(data_all[i]);
                    
                }
            }
            //  _this.$set(_this.git_list,gitlist,true);
            //   _this.$set(_this.vue_list,vuelist,true);
            //   _this.$set(_this.js_list,jslist,true);
            //   _this.$set(_this.js_list,jslist,true);
            //   _this.$set(_this.mini_list,minilist,true);
            //   _this.$set(_this.canvas_list,canvaslist,true);
            //   _this.$set(_this.blend_list,blendlist,true);
            // console.log(gitlist);
            // console.log(vuelist);
            // console.log(jslist);
            // console.log(minilist);
            // console.log(canvaslist);
            // console.log(blendlist);
            // console.log();
            _this.git_list = gitlist;
            _this.vue_lis = vuelist;
            _this.js_list = jslist;
            _this.mini_list = minilist;
            _this.canvas_list = canvaslist;
            _this.blend_list = blendlist;
        },res=>{
             console.log(res);
        })
    },
    mounted(){
        
    },
    methods:{

    },
    components: {
        // myHeader:myHeader
    },
}
</script>
<style>
     /* @import'swiper/dist/css/swiper.min.css'; */
</style>


